import bio from 'bufio';
import { 
	MTX, 
	script, 
	consensus,
	Script, 
	bcrypto
} from '@hansekontor/checkout-components';
const { hashType } = Script;
const { 
    SLP,
    common: { opcodes }
} = script;
const hash256 = bcrypto.Hash256;
const { i64, u64 } = require('n64');

